import { useEffect, useState } from 'react';

export const useAuthorization = (initDataRow) => {
    const [data, setData] = useState();
    
    useEffect(() => {
        fetch('http://localhost:8080/api/set-img', {
            method: 'POST',
            headers: {
                Authorization: `tma ${initDataRow}`
            },
        })
        .then(res => res.text())
        .then(data => setData(data));
    }, [initDataRow]);

    
    return data;
};